.header {
  max-width: 800px;
  margin: 0px auto;
  padding: 15px 20px;
  text-align: center;
}

.header h1 {
  color:  white;
  font-size: 5rem;
  margin: 15px 0px 0px 0px;
}

.header p {
  color: #8e8e8e;
  font-size: 22px;
  margin: 20px;
}

.header ul {
  list-style-type: none;
  margin: 10px auto 30px auto;
  padding: 0px;
}

.header ul li {
  display: inline-block;
  margin: 0px 10px;
}

.header ul li a {
  color: white;
  font-size: 22px;
}

.header ul li a:hover {
  text-decoration: underline;
}