html, body {
  padding: 0;
  margin: 0;

  color: white;
  background-color: hsl(0deg 0% 10%);

  font-family: "EB Garamond", serif, sans-serif;
}

a {
  text-decoration: none;
}

h1,h2,h3,h4,h5,h6 {
  font-weight: normal;
}

.feature {
  padding-top: 40px;
  text-align: center;
}

.feature > span {
  display: block;
  color: #8e8e8e;
  font-size: 18px;
  padding: 10px 0px;
}

.rune_link {
  display: inline-block;
  vertical-align: top;
  margin: 15px;
  color: white;
}

.rune_link:hover {
  opacity: 0.8;
}

.rune_attributes {
  background-color: black;
  width: 400px;
  height: 400px;
  position: relative;
}

 .rune_attributes > span {
  position: absolute;
  right: 10px;
  top: 10px;
}

.rune_attributes ul {
  list-style-type: none;
  margin: 0px;
  padding: 20px;
  text-align: left;
}

.rune_attributes ul li {
  margin: 5px 0px;
}

.rune_attributes ul li span {
  font-size: 20px;
}

table {
  margin-left: auto;
  margin-right: auto;
}
table thead {
  color: white;
  font-size: 20px;
}
table tbody {
  color: white;
  font-size: 20px;
}

svg {
  padding: 10px;
}

.rune_text {
  fill: white; 
  font-family: serif; 
  font-size: 21px;
}